nav {
  height: 80px;
  display: flex;
  padding: 0.5rem calc((100vw - 1000px) / 2);
  z-index: 10;
  justify-content: space-between;
  margin: 1% auto;
}

.link {
  font-family: 'Lato', sans-serif;
  color: #1C3C17;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-decoration: none;
  padding: 0 1rem;
  height: 100%;
  font-size: 1.5rem;
  cursor: pointer;
}
.active {
  color: #854016;
  text-decoration: underline;
}

.menu {
  display: flex;
  align-items: center;
  margin-right: -24px; 
}

.logoSide{
  margin-left: -200px;
}

.logo {
  height: 80%;
  width: 80%;
}

.mobileMenu{
  display: none;
}


@media screen and (max-width: 768px) {
  .menu {
    display: none;
  }
  .mobileMenu {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #1C3C17;
  }
  .logo {
    height: 70%;
    width: 70%;
  }
  .logoSide{
    margin-left: -100px;
  }
}